.stories {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 55.8125rem;

  @media (max-width: 1509.98px) and (min-width: 1024px) {
    height: 45.69rem;
  }

  @media (max-width: 1023.98px) {
    height: 98.375rem;
  }

  &__cards {
    display: flex;
    justify-content: space-between;
    margin-top: 2.625rem;
    padding-left: 0.875rem;
    width: 73.125rem;

    @media (max-width: 1509.98px) and (min-width: 1024px) {
      margin-top: 2.25rem;
      padding-left: 0.585rem;
      width: 62rem;
    }

    @media (max-width: 1023.98px) {
      flex-direction: column;
      row-gap: 1.8125rem;
      margin-top: 2.25rem;
      padding-left: 0;
      width: 18.07rem;
    }
  }

  &__card {
    position: relative;
  }

  &__image {
    width: 23.125rem;
    height: 15.8125rem;

    @media (max-width: 1509.98px) and (min-width: 1024px) {
      width: 19.75rem;
      height: 13.375rem;
    }

    @media (max-width: 1023.98px) {
      width: 18rem;
      height: 13.375rem;
    }
  }

  &__label {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 9.25rem;
    left: -0.875rem;
    width: 5rem;
    height: 5rem;
    background-color: $blue;

    @media (max-width: 1509.98px) and (min-width: 1024px) {
      top: 7.82rem;
      left: -0.585rem;
      width: 4.3rem;
      height: 4.3rem;
    }

    @media (max-width: 1023.98px) {
      top: 7.82rem;
      left: 0;
      width: 4.3rem;
      height: 4.3rem;
    }
  }

  &__title {
    margin-top: 1.875rem;
    line-height: 2.5rem;

    @media (max-width: 1509.98px) and (min-width: 1024px) {
      margin-top: 1.625rem;
      line-height: 2.125rem;
    }

    @media (max-width: 1023.98px) {
      margin-top: 1.625rem;
      line-height: 2.125rem;
    }
  }

  &__text {
    margin: 0.45rem 0;
    width: 23.0625rem;

    @media (max-width: 1509.98px) and (min-width: 1024px) {
      margin: 1.1rem 0;
      width: 19.75rem;
    }

    @media (max-width: 1023.98px) {
      margin: 0.5875rem 0;
      width: 18rem;
    }
  }

  &__counts {
    display: flex;
    column-gap: 2.875rem;
    margin-top: 1.375rem;

    @media (max-width: 1509.98px) and (min-width: 1024px) {
      column-gap: 2.2rem;
      margin-top: 1.1875rem;
    }

    @media (max-width: 1023.98px) {
      column-gap: 1.955rem;
      margin-top: 1.1875rem;
    }
  }

  &__count {
    display: flex;
    align-items: center;
    column-gap: 1rem;

    @media (max-width: 1509.98px) and (min-width: 1024px) {
      column-gap: 0.85rem;
    }

    @media (max-width: 1023.98px) {
      column-gap: 0.85rem;
    }
  }
}

.num {
  margin: 0;
  font-family: $main-font;
  font-size: 2.25rem;
  font-weight: 700;
  line-height: 1.875rem;
  text-align: center;
  color: $white;

  @media (max-width: 1509.98px) and (min-width: 1024px) {
    font-size: 1.5rem;
    line-height: 1.875rem;
  }

  @media (max-width: 1023.98px) {
    font-size: 1.5rem;
    line-height: 1.25rem;
  }
}

.month {
  margin: 0;
  font-family: $main-font;
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.875rem;
  text-align: center;
  color: $white;

  @media (max-width: 1509.98px) and (min-width: 1024px) {
    font-size: 1.125rem;
    line-height: 1.25rem;
  }

  @media (max-width: 1023.98px) {
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
}

.count {
  font-family: $main-font;
  font-size: 0.8125rem;
  font-weight: 400;
  color: $black;

  @media (max-width: 1509.98px) and (min-width: 1024px) {
    font-size: 0.75rem;
  }

  @media (max-width: 1023.98px) {
    font-size: 0.75rem;
  }
}
