.indicator-block {
  background-color: $blue;
  margin: 0 auto;

  @media (max-width: 1023.98px) {
    display: flex;
    justify-content: center;
  }

  &__indicators {
    display: flex;
    justify-content: center;

    @media (max-width: 1023.98px) {
      flex-wrap: wrap;
      width: 18rem;
    }
  }
}

.indicator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 14.625rem;
  height: 15.625rem;
  border-right: 1px solid $white;
  font-family: $main-font;
  font-weight: 700;
  color: $white;

  @media (max-width: 1509.98px) and (min-width: 1024px) {
    width: 12.4rem;
    height: 10.49rem;
  }

  @media (max-width: 1023.98px) {
    width: 9rem;
    height: 7.75rem;
    border-right: 0;
    border-bottom: 1px solid $white;
  }

  &:first-child {
    border-left: 1px solid $white;

    @media (max-width: 1023.98px) {
      border-left: 0;
    }
  }

  &:last-child {
    @media (max-width: 1023.98px) {
      border: 0;
    }
  }

  &__num {
    font-size: 4rem;
    margin: 0;

    @media (max-width: 1509.98px) and (min-width: 1024px) {
      font-size: 3rem;
    }

    @media (max-width: 1023.98px) {
      font-size: 3rem;
    }
  }

  &__name {
    font-size: 1.5rem;
    margin: 0;

    @media (max-width: 1509.98px) and (min-width: 1024px) {
      font-size: 1.125rem;
    }

    @media (max-width: 1023.98px) {
      font-size: 1.125rem;
    }
  }
}
